.Header{
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: larger;
  font-weight: bold;
  color: black;
  background-color: aquamarine;
  padding: 1.5% 1.5%;
}

.Nav{
  background-color: #333;
  padding: 10px 10px;
}

@media screen and (min-width: 740px) {
  ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  li{
    float: left;
    margin-left: 22%;
    display: flex;
    font-weight: bold;
    font-size: 100%;
    background-color: aliceblue;
  }
  
  li:hover{
    background-color:aquamarine;
    color: black;
  } 

  .searchForm{
    padding-left: 7%;
    display: flex;
  }
  
  #search{
    width: 11%;
    height:30px;
  }
}

@media screen and (max-width: 700px) {
  ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  li{
    float: left;
    margin-left: 8%;
    display: flex;
    font-weight: bold;
    font-size: 80%;
  }
  
  li:hover{
    background-color:aquamarine;
    color: black;
  } 

  .searchForm{
    padding-left: 7%;
    display: flex;
  }
  
  #search{
    width: 30%;
    height:20px;
  }
}

.post{
  border-style: solid;
  border-width: 5px;
  margin: 2%;
}

.post:first-child{
  margin-top: 0;
}

.postDate{
  font-size: 0.75rem;
  margin-top:0.25rem;
}

.postBody{
  margin: 1rem 0;
}

.newPostForm{
  position: relative;
  text-align: center;
  font-size: 20px;
  margin: 2rem;
}

.newPostForm label{
  margin: 0 0 2vh 0;
}

.newPostForm input, textarea{
  width:75%;
  padding: .5rem .8rem .5rem .8rem;
  border:0;
  border-radius: 5px;
  font-size: 20px;
  background-color: cornsilk;

}

.newPostForm textarea{
  height: 10vh;
  margin-bottom: 5%;
}

.PostSubmitBtn{
  color: red;
  cursor: pointer;
  border-style: solid;
  border-width: 5px;
  border-color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 2px 2px;
}

.footer{
  text-align: center;
  color: beige;
  background-color:#333;
  padding:1% 0;
  font-size: larger;
  margin-top: 18%;
}

.PostPage{
  text-align: left;
  padding: 1% 20%;
}

.PostPageContent{
  text-align: center;
}

.deletePostBtn{
  color: red;
  cursor: pointer;
  border-style: solid;
  border-width: 5px;
  border-color: black;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 2px 2px;
}